import React, { useState } from 'react';
import { Box, Typography, Chip, Stack, Tooltip } from '@mui/material';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import confetti from 'canvas-confetti';
import '../styles/animations.css';

const initialCategories = {
  Languages: ["JavaScript", "TypeScript", "Python", "Java"],
  "Markup & Styling": ["HTML", "CSS", "Sass"],
  Frameworks: ["React", "Redux", "Node"],
  APIs: ["REST APIs", "GraphQL"],
  Tools: ["Git", "GitHub", "Jest", "Jasmine", "TDD", "Jenkins", "Spinnaker", "Datadog", "Sentry"],
  Databases: ["SQL", "PostgreSQL"],
  "Coding Assistance": ["GitHub Copilot"]
};

const Skills = () => {
  const [categories, setCategories] = useState(initialCategories);

  const isSortedAlphabetically = (arr) => {
    const sortedArr = [...arr].sort();
    return arr.join() === sortedArr.join();
  };

  // Fire confetti at multiple random origins to cover the whole screen.
  const triggerFullScreenConfetti = () => {
    const totalCalls = 10; // Number of confetti bursts across the screen.
    for (let i = 0; i < totalCalls; i++) {
      confetti({
        particleCount: 20,
        spread: 70,
        origin: { x: Math.random(), y: Math.random() }
      });
    }
  };

  const handleDragEnd = (result) => {
    if (!result.destination) return;

    // Only handle moves within the same category.
    if (result.source.droppableId !== result.destination.droppableId) return;

    const category = result.source.droppableId;
    const items = Array.from(categories[category]);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);
    
    setCategories((prev) => ({
      ...prev,
      [category]: items
    }));

    // If the category is now sorted alphabetically, trigger full screen confetti.
    if (isSortedAlphabetically(items)) {
      triggerFullScreenConfetti();
    }
  };

  return (
    <Box sx={{ mb: 4 }}>
      <Tooltip title="Sort the skills alphabetically to reveal a hidden easter egg!" arrow>
        <Typography variant="h5" gutterBottom sx={{ textAlign: 'center' }}>
          Skills
        </Typography>
      </Tooltip>
      <DragDropContext onDragEnd={handleDragEnd}>
        {Object.entries(categories).map(([categoryName, skills]) => (
          <Box key={categoryName} sx={{ mb: 3 }}>
            <Typography variant="h6" gutterBottom data-category-header={categoryName}>
              {categoryName}
            </Typography>
            <Droppable droppableId={categoryName} direction="horizontal">
              {(provided) => (
                <Stack
                  direction="row"
                  spacing={1}
                  flexWrap="wrap"
                  ref={provided.innerRef}
                  {...provided.droppableProps}
                >
                  {skills.map((skill, index) => (
                    <Draggable key={skill} draggableId={skill} index={index}>
                      {(provided) => (
                        <Chip
                          label={skill}
                          sx={{ mb: 1, cursor: 'grab' }}
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                        />
                      )}
                    </Draggable>
                  ))}
                  {provided.placeholder}
                </Stack>
              )}
            </Droppable>
          </Box>
        ))}
      </DragDropContext>
    </Box>
  );
};

export default Skills;